*{
  box-sizing: border-box;
}
body{
  background-color: #ffffff;
}
.container{
  padding: 10px;
  /* background-color: red; */
  max-width: fit-content;
  margin:50px auto;
}
.btn{
  border:none;
  padding: 7px 15px;
  font-size: 18px;
  cursor: pointer;
}
.btn-add{
  background: #194064;
  color:white;

}

.addContainer{
  position: absolute;
  background-color: rgba(0,0,0,0.2);
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.addContainer form{
  width: 420px;
  background-color: white;
  display: flex;
  flex-direction: column;
  padding: 50px 40px;
  box-shadow:5px 5px 10px rgba(0,0,0,0.2) ;
  border-radius: 5px;
}
.addContainer form label{
  font-size: 18px;
}
.addContainer form input{
  font-size: 18px;
  padding: 5px;
  margin-top: 5px;
  margin-bottom: 10px;
}
.addContainer form button{
  background-color: royalblue;
  color:white;
  font-weight: 500;
  margin-top: 20px;
}
.addContainer form .close-btn{
  margin-left: auto;
  font-size: 18px;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
   cursor: pointer;
   color: rgb(0, 0, 0);
}

.tableContainer{
  margin-top: 50px;
  box-shadow: 5px 5px 10px rgba(74, 62, 241, 0.2);
  border-radius: 5px;
}
 
.tableContainer table thead tr{
  background-color: #fff0d1;
  
}
.tableContainer table thead tr th,
.tableContainer table tbody tr td{
  min-width: 250px;
  padding: 7px;
  text-align: left;
}
.tableContainer table tbody tr{
  border : 1px solid #c9c5c2;
}
.tableContainer table tbody tr td{
  border-bottom: 1px solid #676262;
}
.btn-edit,
.btn-delete{
  font-size: 16px;
  padding: 5px 10px;
  margin:0px 10px;
}
.btn-edit{
  background-color: #5f5f60;
  color:rgb(255, 255, 255);
}
.btn-delete{
  background-color: #ff433a;
  color:rgb(255, 255, 255);
}